<template>
  <div class="product_list">
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">home</el-breadcrumb-item>
        <el-breadcrumb-item>检索</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="product_main">
      <div class="choose" v-for="(item, i) of 4" :key="i + 'a'">
        <div class="choose_name">细胞类型：</div>
        <div class="choose_content">
          <span v-for="(item, ii) of 4" :key="ii + 'b'">选择</span>
        </div>
      </div>
      <div class="line"></div>
      <div class="select_all" v-for="(item, k) of 4" :key="k + 'c'">
        <div class="select_a">
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="select_b">
          <el-input
            placeholder="请输入内容"
            v-model="input0"
            class="input-with-select"
          >
            <el-select v-model="select0" slot="prepend" placeholder="请选择">
              <el-option
                v-for="item in options0"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-input>
        </div>
      </div>
      <div class="btn_all">
        <div class="btn to_serach">检索</div>
        <div class="btn to_reset">重置</div>
      </div>
      <div
        class="all_product"
        v-for="(item, i) of contentModuleItemList"
        :key="i + 'd'"
      >
        <img src="../../assets/img/pg1.png" alt="" />
        <div class="product_detail">
          <div class="product_title">
            这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息
          </div>
          <div class="product_content">2020/02/09</div>
          <div class="product_content">
            这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息这里是标题信息
          </div>
          <div class="product_content">分发类型</div>
        </div>
        <div class="share">显现分享</div>
        <!-- <div class="link" v-if="i == 8">联系注册机构</div> -->
        <div class="add_num" v-if="item.quantity > 0">
          <img class="all_cart" src="../../assets/img/01@3x.png" alt="" />
          <div class="add_num_btn" @click="addCart(item, -1)"></div>
          <div class="add_num_num">{{ item.quantity }}</div>
          <div class="add_num_btn" @click="addCart(item, 1)"></div>
        </div>
        <img
          class="cart"
          v-else
          src="../../assets/img/cart.png"
          alt=""
          @click="addCart(item, 1)"
        />
      </div>
      <div class="paginaton">
        <el-pagination
          :background="false"
          :pager-count="9"
          layout="prev, pager, next"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :total="80"
        >
        </el-pagination>
      </div>
    </div>
    <div class="bottom"></div>
  </div>
</template>
<script>
import {
  queryModuleContent,
  addCart,
  queryCart,
  deleteCart,
} from "/api/user.js";
export default {
  data() {
    return {
      options: [
        {
          value: "1",
          label: "AND",
        },
        {
          value: "2",
          label: "NOT",
        },
        {
          value: "3",
          label: "OR",
        },
      ],
      options0: [
        {
          value: "1",
          label: "餐厅名",
        },
        {
          value: "2",
          label: "订单号",
        },
        {
          value: "3",
          label: "用户电话",
        },
      ],
      value: "AND",
      input0: "",
      select0: "1",
      contentModuleItemList: [],
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val;
      this._getSearch();
      this.loading = true;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this._getSearch();
      this.loading = true;
    },
    queryModuleContent() {
      let params = {
        params: {
          moduleShortName: "hot_product_1",
        },
      };
      queryModuleContent(params).then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          let { contentModuleItemList } = res.data;
          contentModuleItemList.forEach((item) => {
            item.quantity = 0;
          });
          this.queryCart(contentModuleItemList);
        }
      });
    },
    addCart(item, num) {
      const { contentId } = item;
      let paramsAdd = new FormData();
      paramsAdd.append("productId", contentId);
      paramsAdd.append("quantity", num);
      paramsAdd.append("selectStatus", 1);
      addCart(paramsAdd).then((res) => {
        if (res && res.code && res.code == "200") {
          this.queryCart(this.contentModuleItemList);
        }
      });
    },
    queryCart(list) {
      let that = this;
      list = list || that.contentModuleItemList;
      queryCart().then((res) => {
        if (res && res.code && res.code == "200" && res.data) {
          let showCarts = res.data;
          showCarts.forEach((item) => {
            list.forEach((content) => {
              if (item.productId == content.contentId) {
                content.quantity = item.quantity;
              }
            });
          });
          that.contentModuleItemList = list;
          console.log(that.contentModuleItemList);
        }
      });
    },
  },
  created() {
    this.queryModuleContent();
  },
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.product_list {
  background: #f9f9f9;
}
.bottom {
  height: 24px;
}
.product_main {
  width: 1200px;
  background: #ffffff;
  margin: 0 auto;
  padding: 37px 27px 36px 21px;
  .choose {
    height: 22px;
    line-height: 22px;
    display: flex;
    margin-bottom: 18px;
    .choose_name {
      width: 96px;
      font-size: 14px;
      color: rgba(0, 0, 0, 0.85);
      font-weight: bold;
    }
    .choose_content {
      font-size: 14px;
      color: rgba(0, 0, 0, 0.65);
      span {
        margin-right: 40px;
      }
    }
  }
  .line {
    width: 1152px;
    border-top: 1px solid #e9e9e9;
  }
  .select_all {
    display: flex;
    margin-top: 20px;
    .select_a {
      width: 120px;
      height: 32px;
      .el-select {
        width: 100%;
        height: 100%;
      }
      .el-input {
        width: 100%;
        height: 100%;
      }
    }
    .select_b {
      width: 600px;
      height: 32px;
      margin-left: 20px;
      .el-input-group__prepend .el-input {
        width: 120px;
      }
    }
  }
  .btn_all {
    margin: 24px 0;
    padding-bottom: 36px;
    display: flex;
    border-bottom: 1px solid #e9e9e9;
    .btn {
      border-radius: 2px;
      width: 98px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      font-size: 16px;
      margin-right: 24px;
    }
    .to_serach {
      background: #0695d4;
      color: #ffffff;
    }
    .to_reset {
      color: #595959;
      background: #ffffff;
      border: 1px solid #d9d9d9;
    }
  }
  .all_product {
    width: 1152px;
    height: 208px;
    border-bottom: 1px solid #e9e9e9;
    display: flex;
    img {
      width: 220px;
      height: 160px;
      margin-top: 25px;
      margin-right: 16px;
    }
    .product_detail {
      width: 603px;
      .product_title {
        font-size: 16px;
        color: #333333;
        margin-top: 26px;
        line-height: 22px;
        font-weight: bold;
      }
      .product_content {
        margin-top: 8px;
        line-height: 20px;
        font-size: 14px;
        color: #787878;
        width: 580px;
      }
    }
    .share {
      margin-left: 44px;
      margin-top: 94px;
      font-size: 14px;
      color: #333333;
      line-height: 20px;
      width: 60px;
    }
    .link {
      font-size: 14px;
      color: #0695d4;
      margin-left: 96px;
      margin-top: 94px;
    }
    .cart {
      width: 40px;
      height: 36px;
      margin-left: 122px;
      margin-top: 86px;
      cursor: pointer;
    }
    .add_num {
      margin-left: 60px;
      margin-top: 92px;
      height: 24px;
      display: flex;
      position: relative;

      .all_cart {
        width: 138px;
        height: 24px;
        position: absolute;
        left: 0;
        top: -24px;
      }
      .add_num_btn {
        width: 32px;
        height: 24px;
        margin-top: 0;
        margin-right: 5px;
        position: relative;
        cursor: pointer;
      }
      .add_num_num {
        width: 58px;
        height: 24px;
        line-height: 24px;
        text-align: center;
        border-radius: 1.5px;
        font-size: 14px;
        color: #333333;
        margin: 0 5px;
        position: relative;
      }
    }
  }
}
</style>



